import React from "react";
import TopBanner from '../../components/TopBanner'
import Preview from '../../components/Preview'
import Helmet from "react-helmet";

export default function Efficiency({ data }) {
  const { edges: posts } = data.allMarkdownRemark;
  return (
    <div className="preview-feed">
      <Helmet title={`Самые эффективные упражнения`} />
      <TopBanner style={efficiencyStyle} title={'Самые эффективные упражнения'} />
      {posts
        .filter(post => post.node.frontmatter.efficiency >= '7')
        .map(({ node: post }) => {
          return (
            <Preview post={post} />
          );
        })}
    </div>
  );
}

const efficiencyStyle = {
  backgroundImage: 'linear-gradient(to right top, #46a36e, #52bb68, #6ad35b, #8cea44, #b5ff10)'
};

export const pageQuery = graphql`
  query EfficiencyQuery {
    allMarkdownRemark(sort: { order: DESC, fields: [frontmatter___date] }) {
      edges {
        node {
          excerpt(pruneLength: 180)
          id
          frontmatter {
            title
            date(formatString: "MMMM DD, YYYY")
            path
            image
            image_2
            image_3
            image_4
            filterby
            efficiency
            injury
            bodypart
            filtertype
            type
            filterinventory
            inventory
          }
        }
      }
    }
  }
`;



